const url =`https://back-end-demo2.onrender.com/students`;
export  async function getAll() {
    try {
        const response = await axios.get(url);
        // console.log("--------call api----------------");
        // console.log(response.data);
        return response.data; // Tr v d liu t API
    } catch (error) {
        console.error("Error fetching data:", error);
        return []; // Tr v mng rng khi li
    }
}

export async function searchByName(name) {
    console.log(name)
    try {
        const response = await axios.get(url +`?name_like=${name}`);
        console.log("--------search call api----------------");
        console.log(response.data);
        return response.data; // Tr v d liu t API
    } catch (error) {
        console.error("Error fetching data:", error);
        return []; // Tr v mng rng khi li
    }

}
export async function addNew(student) {
    try {
        const response = await axios.post(url,student);
        console.log("--------call api thm mi----------------");
        console.log(response.data);
    } catch (e) {
        console.error("add is error", e);
    }
}

