import axios from "axios";

export async function loginAccount(account) {
    try {
        const response = await axios.get("https://back-end-demo2.onrender.com/accounts");
        const currentAccount = response.data.find(e =>e.username == account.username&&e.password==account.password);
        console.log("=============service=============")
        console.log(currentAccount)
        return currentAccount; // Tr v d liu t API
    } catch (error) {
        console.error("Error fetching data:", error);
        return null; // Tr v mng rng khi li
    }
}